import React from "react"
import Layout from "../components/layout"
import "../components/courses.css"
import SEO from "../components/seo"

import { Link } from "gatsby"
function courses() {
  let coursesList = {
    js: {
      id: "js",
      name: "JS",
      title: "The Note-worthy Javascript course.",
      description: `I have been using Javascript for 3+ years. Javascript is my Favourite Language. Python is cool but come on Javascript is everywhere. It's easy to learn and comparatively fast. My Methodology: 1- Learn the very basics
        2- Solve algorithm challenges
        3- Work on simple projects
        4- Build Practical Life Projects
        5- Solidify`,
      price: "₹800/",
    },
    html: {
      id: "html",
      name: "HTML & CSS",
      title: "HTML and CSS course for Beginners.",
      description:
        "HTML and CSS are the basics of web development or the two eyes of web development. It's not just easy to learn, It's fun to learn You can build 1000's of real-life projects while still learning. This course is for absolute Beginners and for Intermediates who want to solidify",
      price: "₹600/",
    },
    python: {
      id: "python",
      name: "PYTHON",
      title: "Learn Python and Land your first Dreamjob",
      description:
        "Python A-Z. When I say A-Z, I mean it. Python is one of the popular languages out there and it's easy to learn too. So why not learn python and land your dream job in just 6 months. Start from the basics and go all the way to creating your own application with python",
      price: "₹800/",
    },
  }

  return (
    <Layout>
      <SEO
        title="computer Programming courses for beginners offered by Deepan Chakravarthi"
        description="computer programming courses for beginners offered by Deepan chakravarthi."
      />
      <div className="courses-container">
        <div className="title">
          <h1>COURSES</h1>
        </div>
        <h4 style={{ textAlign: `center` }}>
          I'm not selling pre-recorded videos. These courses are synchronous
          events. You and I will be able to communicate with voice, video,
          whiteboard and much more.
        </h4>

        <div className="courses-wrapper">
          {/* javascript */}
          <div id="js" className="course">
            <div className="logo">{coursesList.js.name}</div>
            <div className="course-info-div">
              <div className="course-name">
                <h2>{coursesList.js.title}</h2>
              </div>
              <div className="course-description">
                <p>{coursesList.js.description}</p>
              </div>
              <div className="course-button">
                <Link to="/application">
                  <button>Take this course</button>
                </Link>
              </div>
            </div>
            <div className="price-div">
              <p>
                {coursesList.js.price}
                <span style={{ fontSize: `16px` }}>hour.</span>
              </p>
            </div>
          </div>
          {/* javascript */}
          <div id="html" className="course">
            <div className="logo">
              HTML <br /> &amp; <br /> CSS
            </div>
            <div className="course-info-div">
              <div className="course-name">
                <h2>{coursesList.html.title}</h2>
              </div>
              <div className="course-description">
                <p>{coursesList.html.description}</p>
              </div>
              <div className="course-button">
                <Link to="/application">
                  <button>Take this course</button>
                </Link>
              </div>
            </div>
            <div className="price-div">
              <p>
                {coursesList.html.price}
                <span style={{ fontSize: `16px` }}>hour.</span>
              </p>
            </div>
          </div>
          {/* html */}

          {/* python */}
          <div id="python" className="course">
            <div className="logo">{coursesList.python.name}</div>
            <div className="course-info-div">
              <div className="course-name">
                <h2>{coursesList.python.title}.</h2>
              </div>
              <div className="course-description">
                <p>{coursesList.python.description}</p>
              </div>
              <div className="course-button">
                <Link to="/application">
                  <button>Take this course</button>
                </Link>
              </div>
            </div>
            <div className="price-div">
              <p>
                {coursesList.python.price}
                <span style={{ fontSize: `16px` }}>hour.</span>
              </p>
            </div>
          </div>
          {/* python */}
        </div>
      </div>
    </Layout>
  )
}

export default courses
